import React from 'react'
import {NavLink} from "react-router-dom";

import styles from "./NavbarItems.module.css";

const NavbarMenuItem = ({to, text}) => {
    return (
        <li className={styles.navbar_menu_item}>
            <NavLink exact to={to} className={styles.navbar_menu_link} activeClassName={styles.active}>{text}</NavLink>
        </li>
    )
}

export default NavbarMenuItem;