import React, {Suspense} from 'react';
import { useHistory } from "react-router-dom";
import styles from './MovieCard.module.css'
const MovieCard = ({id,poster_path, overview, popularity, status, original_title, original_name}) => {

    const img = poster_path ? `https://image.tmdb.org/t/p/w500${poster_path}` : "";
    const name = original_title ? original_title : original_name;

    const history = useHistory();

    return (
           <>
                        <button  onClick={() =>
                            history.goBack()
                        } className={styles.btnClose}>
                            Go to back
                        </button>
                <div className={styles.filmCard}>
                    <div className={styles.posterFilm}>
                        <img src={img} alt={name} />
                    </div>
                    <div className={styles.description}>
                        <h2 className={styles.filmName}>{name}</h2>
                        <p className={styles.status}>Status: {status}</p>
                        <p className={styles.popularity}>Popularity: {popularity}</p>
                        <p className={styles.overview}>{overview}</p>
                    </div>
                </div>
        </>
    );
};

export default MovieCard;