import React from 'react';
// import PropTypes from 'prop-types';

import styles from "../../../navbar/components/NavbarItems/NavbarItems.module.css";
import {NavLink, Route} from "react-router-dom";
import CastFilm from "../CastFilm";
import ReviewsFilm from "../ReviewsFilm";

const NavCard = ({id}) => {


    return (
        <div>
            <ul>
                    <li className={styles.navbar_menu_item}>
                        <NavLink  to={`/movie/${id}/cast`} className={styles.navbar_menu_link} activeClassName={styles.active}>Cast</NavLink>
                    </li>
                    <li className={styles.navbar_menu_item}>
                        <NavLink  to={`/movie/${id}/reviews`} className={styles.navbar_menu_link} activeClassName={styles.active}>Reviews</NavLink>
                    </li>
            </ul>
        </div>
    );
}


NavCard.propTypes = {};

export default NavCard;