import React, {Component} from 'react';

import  {fetchReviewsFilm} from "../../service/movies-service/movies-service"

import ReviewsFilmItem from "../ReviewsFilmItem"

class ReviewsFilm extends  Component{

    state = {
        reviews: [],
        loading: false,
        error: null,
    }

    componentDidMount() {
        this.setState({
            loading: true,
        })
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const {fetchReviewsFilms} = this
        const {loading} = this.state
        if (loading){
            fetchReviewsFilms()
        }
    }

    fetchReviewsFilms = () => {
        const  {id} = this.props
        fetchReviewsFilm(id)
            .then(res => {
                const  {results} = res;
                this.setState({
                    reviews: results,
                    loading: false
                })
            })
            .catch(error => error)
    }
    render() {
        const {reviews} = this.state
        return (
            <div>
                <ReviewsFilmItem reviews={reviews}/>
            </div>
        );
    }
};

export default ReviewsFilm;