import React, {Component} from 'react';

import {fetchCastFilm} from "../../service/movies-service/movies-service";
import CastFilmItem from "../CastFilmItem";

class CastFilm extends Component {

    state = {
        cast: [],
        loading: false,
    }

    componentDidMount() {
        this.setState({
            loading:true,
        })
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        const {fetchCast} = this;
        const {cast,loading} = this.state
        if(loading){
            console.log(fetchCast())
            console.log(cast)
        }
    }

    fetchCast = () => {
        const  {id} = this.props
        fetchCastFilm(id)
            .then(res => {
                const {data} = res;
                this.setState({
                    cast: data.cast,
                    loading: false,
                })
            })
            .catch(error => error)
    }

    render() {
        const {cast} = this.state
        console.log(cast)
        return (
            <div>
                <ul>
                    <CastFilmItem cast = {cast}/>
                </ul>
            </div>
        );
    }
};

export default CastFilm;