import React from 'react';

import styles from "./ReviewsFilmItem.module.css"

const ReviewsFilmItem = ({reviews}) => {
    const reviewsElem = reviews.map(({author, content}) => {
        return (
            <>
                <h3>Author: {author}</h3>
                <div className={styles.content} >
                    <p>{content}</p>
                </div>
            </>
        )
    })
    return <>{reviewsElem}</>
};

export default ReviewsFilmItem;