import React, {Component} from 'react';
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";

import {fetchFilmsOnId} from '../../service/movies-service/movies-service'
import MovieCard from "../MoviCard";
import ReviewsFilm from "../ReviewsFilm";

import {state} from './initialState'
import CastFilm from "../CastFilm";
import NavCard from "../NavCard/NavCard";

class OneMoviePage extends Component {
    state = {
        ...state
    }

    componentDidMount() {
        this.setState({loading: true})
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const {fetchFilm} = this;
        const {loading} = this.state;

        if (loading) {
            fetchFilm()
        }
    }

    fetchFilm = () => {
        const {id} = this.props.match.params

        fetchFilmsOnId(id)
            .then(res => this.setState({
                    movie: res,
                    loading: false,
                })
            )
            .catch(error => this.setState({error, loading: false}))
    }

    render() {
        const {movie} = this.state;
        const {id} = this.props.match.params

        return (
            <>
                <MovieCard  {...movie}/>
                <NavCard id={id}/>

                <Route path="/movie/:id/cast" exact>
                    <CastFilm id={id}/>
                </Route>

                <Route path="/movie/:id/reviews" exact >
                    <ReviewsFilm id={id} />
                </Route>
            </>
        )
    }
}
                export default OneMoviePage;