import React from 'react';

const CastFilmItem = ({cast}) => {
    const castElem = cast.map(({profile_path,original_name,character}) => {
        const img =  `https://image.tmdb.org/t/p/w500${profile_path}`
        return (
            <li>
                <img src={img} alt={original_name} width={200} height={300}/>
                <h3>{original_name}</h3>
                <p>{character}</p>
            </li>
        )
    })
    return <>{castElem}</>
};

export default CastFilmItem;