import {instance} from "../../../../shared/service/basic-http-service";

export const fetchTrendingFilms= async () => {
    const {data} = await instance.get(`/trending/all/day`)
    return data
}
export const fetchFilmWords=  (query) => {
    return instance.get(`search/movie?query=${query}`)
}
export const fetchFilmsOnId= async (id) => {
    const {data} = await instance.get(`/movie/${id}`)
    return data
}

export const fetchCastFilm=  (id) => {
    return  instance.get(`/movie/${id}/credits`)
}

export const fetchReviewsFilm= async (id) => {
    const {data} = await instance.get(`/movie/${id}/reviews`)
    return data
}
