import React, {lazy, Suspense} from "react";
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";

import Navbar from "./client/navbar/components/Navbar";
import MoviePages from "./client/Movies/pages/MoviesPage";


const TrendingMoviePage = lazy(() => import('./client/Movies/pages/TrendingMoviePage'))
const MovieSearchPage = lazy(() => import("./client/Movies/pages/MovieSearchPage"))

function App() {
    return (
        <>
            <Router>
                <Navbar/>
                <Suspense fallback={<p>Loading page...</p>}>
                    <Switch>

                        <Route path="/" exact>
                            <TrendingMoviePage title="Trending today"/>
                        </Route>
                        <Route path="/movies" exact>
                            <MovieSearchPage title="Search movie"/>
                        </Route>
                        <Route path="/search/movie" exact>
                            <MovieSearchPage title="Search movie"/>
                        </Route>

                        <Route path="/movie/:id" exact component={MoviePages}/>
                        <Route path="/movie/:id/cast" exact component={MoviePages}/>
                        <Route path="/movie/:id/reviews" exact component={MoviePages}/>
                        <Route render={()=> {
                            return <>
                            <img src="https://znaiwifi.com/wp-content/uploads/2018/01/hqdefault.jpg"/>
                            </>
                        }
                        }/>
                    </Switch>



                </Suspense>
            </Router>
        </>

    );
}

export default App;
